.App {
  text-align: left;
  /* align-content: center; */
  /* align-items: center; */
  width: 100%
}

@font-face {
  font-family: 'Klima';
  src: local('Klima'), url(./fonts/klima/klima-regular-web.woff) format('woff');
}

@font-face {
  font-family: 'Klima Heavy';
  src: local('Klima Heavy'), url(./fonts/klima/klima-heavy-web.woff) format('woff');
}

@font-face {
  font-family: 'Klima Bold';
  src: local('Klima Bold'), url(./fonts/klima/klima-bold-web.woff) format('woff');
}

div li {
  display: inline-block;
  font-size: 40;
}

.Content-and-map-container div{
  display: inline-block;
}

h1 {
  font-size: 25px;
  margin-right: 20px;
}

a:link {
  color: black;
  -webkit-text-decoration-line: none;
          text-decoration-line: none;
  font-size: 25px;
  font-weight: bold;
}

a:hover {
  -webkit-text-decoration-line: underline;
          text-decoration-line: underline;
  font-style: italic;
}

a:visited {
  color: black;
}

h2 {
  font-size: 15px;
  font-style: italic;
  
  -webkit-animation: fadein 2s linear infinite alternate;
  animation: fadein 2s linear infinite alternate;
}

@-webkit-keyframes fadein {
  from { opacity: 0.2; }
  to   { opacity: 1; }
}

@keyframes fadein {
  from { opacity: 0.2; }
  to   { opacity: 1; }
}

.imageMapper {
  height: 100px;
}

.App-intro {
  -ms-flex-direction: row;
      flex-direction: row;
  font-size: large;
}

.Map-image {
  /* width: 40%;
  align-self: center; */
  display: inline-block;
}

.Story-content-container {
  margin: 50px;
  margin-top: 0px;
  display: inline-block;
}

.Map-container {
  text-align: left;
  -ms-flex-direction: row;
      flex-direction: row;
}

nav {
  overflow: hidden;
  padding: 1em;
}
nav li {
  color: #fff;
}
nav li:visited {
  color: #fff;
}
nav .navWide {
  display: none;
  margin: 0 auto;  
}

nav .navWide .wideDiv li {
  text-decoration: none;
  display: inline-block;
  padding: 0 0.5em 0 0  ;
}
nav .navNarrow i {
  float: left;
  cursor: pointer;
  margin-right: 1em;
}
nav .navNarrow .narrowLinks {
  display: none;
}
nav .navNarrow .narrowLinks li {
  font-size: 12;
  text-decoration: none;
  display: block;
  float: left;
  clear: left;
  padding: 0.5em 0;
}
@media (min-width: 480px) {
  nav .navWide {
    display: block;
  }
  nav .navNarrow {
    display: none;
  }
}



